p{
  font-size:1.2rem;
  line-height:1.5;
  margin:0;
}

.App{
  max-width: 900px;
  width:100%;
  margin:0px auto;
}

header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding:32px 24px 0px;
}

label{
  cursor:pointer;
}

.customSelect{
  display: flex;
  align-items: center;
  cursor:pointer;
}

.customSelect svg{
  pointer-events: none;
}

select{
  border:none;
  padding:8px;
  font-size: 1rem;

  /* Removes the default <select> styling */
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  color:inherit
}

.content-categories{
  display: flex;
  overflow: auto;
  align-items: center;
  gap:16px;
  padding:4px 24px;
  margin:24px 0px;
}
.content-categories::-webkit-scrollbar {
  display: none;
}

.custom-input-radio input{
  display: none;
}

.custom-input-radio span{
  display: flex;
  align-items: center;
  gap: 8px;
  background:var(--white);
  border:1px solid var(--gray-light);
  padding:6px 16px;
  border-radius:50px;
  font-weight: bold;
  white-space: nowrap;
}
.custom-input-radio span svg{
  max-height: 24px;
}

.custom-input-radio input:checked + span{
  background: var(--primary-color-light);
  border-color: var(--primary-color);
  color: var(--primary-color);
}
.custom-input-radio input:checked + span svg path{
  fill: var(--primary-color);
}


.content-table{
  padding:0px 0px;
}

@media screen and (min-width: 700px) {
  .content-table{
    padding:0px 24px 90px;
  }
}
  


table {
  width:100%;
  font-size: 1rem;
  text-align: left;
}

table tr{
  outline: 1px solid var(--gray-light);
}
th, td{
  padding:16px;
}

th:first-child{
  padding:8px 0px 8px 24px;
}
td:first-child{
  padding:16px 0px 16px 24px;
}

td span{
  display: flex;
}
td > span{
  margin-bottom: 4px;
}

.menu{
  position: fixed;
  display: flex;
  bottom:0px;
  left:0px;
  padding:0px 24px;
  border:1px solid #e7e7e7;
  width: 100%;
  background-color: var(--white);
  box-shadow: 2px 2px 6px rgba(0,0,0,0.1);
}
.menu label{
  position: relative;
  display: flex;
  width:100%;
}
.menu label span{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap:0px;
  padding: 16px 16px;
  width:100%;
  gap:4px;
}
.menu label input{display:none;}
.menu label input:checked + span{ 
  background: var(--primary-color-light);
  color: var(--primary-color);
}
.menu label input:checked + span:after{
  content:'';
  position: absolute;
  top:0px;
  left:0px;
  width: 100%;
  height: 4px;
  background:var(--primary-color);
 }
.menu label input:checked + span svg path{ 
  stroke: var(--primary-color);
}
.menu label svg{
  width: 21px;
}